
import {
  defineComponent,
  onMounted,
  reactive,
  toRefs,
  getCurrentInstance,
} from 'vue'

export default defineComponent({
  name: 'df-modal',
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    mask: {
      type: Boolean,
      default: false,
    },
    modalStyle: {
      type: Object,
      default: () => {
        return {}
      },
    },
    headerStyle: {
      type: Object,
      default: () => {
        return {
          color: 'red',
        }
      },
    },
    contentStyle: {
      type: Object,
      default: () => {
        return {}
      },
    },
    footerStyle: {
      type: Object,
      default: () => {
        return {}
      },
    },
    headColor: {
      type: String,
      default: '#000',
    },
    headText: {
      type: String,
      default: '',
    },
    headTextColor: {
      type: String,
      default: '#fff',
    },
    contentText: {
      type: String,
      default: '',
    },
    contentTextColor: {
      type: String,
      default: '#000',
    },
    position: {
      type: String,
      default: 'top',
    },
    actionBtn: {
      type: Boolean,
      default: false,
    },
    showHead: {
      type: Boolean,
      default: false,
    },
    confirmText: {
      type: String,
      default: '',
    },
    cancelText: {
      type: String,
      default: '',
    },
  },
  setup(props, ctx) {
    const instance = getCurrentInstance()

    const state = reactive({
      dfModalHeight: 0,
      modalShow: props.show,
      mask: props.mask,
    })

    const confirm = () => {
      state.modalShow = false
      state.mask = false
      ctx.emit('confirm', {
        msg: 'modal-confirm',
      })
    }
    const cancel = () => {
      state.modalShow = false
      state.mask = false
      ctx.emit('cancel', {
        msg: 'modal-cancel',
      })
    }

    onMounted(() => {
      state.dfModalHeight = (instance?.refs.dfModal as any).offsetHeight
    })

    return {
      confirm,
      cancel,
      ...toRefs(state),
    }
  },
})
