
import { defineComponent } from 'vue'
import magnifier from './directives/magnifier'

export default defineComponent({
  name: 'df-magnifier',
  props: {
    imgWidth: {
      type: Number,
      default: 375,
    },
    imgHeight: {
      type: Number,
      default: 400,
    },
    magWidth: {
      type: Number,
      default: 150,
    },
    magHeight: {
      type: Number,
      default: 150,
    },
    blank: {
      type: Boolean,
      default: false,
    },
    link: {
      type: String,
    },
    imgAlt: {
      type: String,
    },
    imgUrl: {
      type: String,
    },
  },
  directives: {
    magnifier,
  },
  setup(props) {
    return {}
  },
})
