
import { ref, defineComponent } from 'vue'

export default defineComponent({
  name: 'df-tree-sub-menu',
  setup() {
    const subShow = ref(false)

    const showSub = () => {
      subShow.value = true
    }

    const hideSub = () => {
      subShow.value = false
    }

    return {
      showSub,
      hideSub,
      subShow,
    }
  },
})
