
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'df-carousel-dot',
  props: {
    itemLen: Number,
    currentIndex: Number,
    showDot: {
      type: Boolean,
      default: true,
    },
    dotBgColor: {
      type: String,
      default: '#ff5000',
    },
  },

  setup(props, ctx) {
    const dotClick = (index: number) => {
      ctx.emit('dotClick', index)
    }

    return {
      dotClick,
    }
  },
})
