
import {
  ComponentInternalInstance,
  defineComponent,
  getCurrentInstance,
  reactive,
  toRefs,
  watch,
} from 'vue'

export default defineComponent({
  name: 'df-carousel-item',

  setup: function () {
    // 
    const instance = getCurrentInstance()

    const state = reactive({
      selfIndex: (instance as ComponentInternalInstance).vnode.key, // 
      currentIndex: (instance as any).parent.ctx.currentIndex, // 
      animation: (instance as any).parent.ctx.animation,
    })

    // 
    watch(
      () => {
        return (instance as any).parent.ctx.currentIndex
      },
      (value) => {
        state.currentIndex = value
      },
    )

    return {
      ...toRefs(state),
    }
  },
})
