
import { defineComponent } from 'vue'
export default defineComponent({
  name: 'df-carousel-director',
  props: {
    dir: String,
  },

  setup(props, ctx) {
    const dirClick = (dir: string | undefined) => {
      ctx.emit('dirClick', dir)
    }

    return {
      dirClick,
    }
  },
})
